.popup__form {
  width: 430px;
  padding: 36px 36px 38px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 768px) {
    .popup__form {
      width: 282px;
      padding: 25px 22px;
    }
  }